*{
    padding:0;
    margin:0;
}
a{
    text-decoration:none;
}
p,h1,h2{
    cursor:default;
}
/* Header */
.header-wrapper{
    height:80px;
    background-color:rgba(41, 14, 14, 0.678);
    position:fixed;
    top:0;
    left:0;
    z-index:10;
    width:100%;
}
.container{
    max-width:1300px;
    margin:0 auto;
}
.header-parts-wrapper{
    display:flex;
    flex-wrap:wrap;
}
.header-parts-wrapper a{
    display:block;
}
.header-parts-wrapper img{
    width:80px;
}
.header-parts-wrapper h1{
    line-height:80px;
    margin:0 auto;
    font-family:Georgia, 'Times New Roman', Times, serif;
    color:white;
    opacity:0.9;
    cursor:default;
    letter-spacing:2px;
}
/* header-hamburger_menu */
/* nav */
nav {
    display: block;
    position: fixed;
    top: 0;
    left: -300px;
    bottom: 0;
    width: 300px;
    background: #ffffff;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    transition: all .5s;
    z-index: 3;
    opacity: 0;
  }
  .open nav {
    left: 0;
    opacity: 1;
  }
  nav .inner {
    padding: 25px;
  }
  nav .inner ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  nav .inner ul li {
    position: relative;
    margin: 0;
    border-bottom: 1px solid #333;
  }
  nav .inner ul li a {
    display: block;
    color: #333;
    font-size: 14px;
    padding: 1em;
    text-decoration: none;
    transition-duration: 0.2s;
  }
  nav .inner ul li a:hover {
    background: #e4e4e4;
  }
  @media screen and (max-width: 767px) {
    nav {
      left: -220px;
      width: 220px;
    }
  }
  /*.toggle_btn*/
  .toggle_btn {
    display: block;
    position: fixed;
    top: 25px;
    right: 30px;
    width: 30px;
    height: 30px;
    transition: all .5s;
    cursor: pointer;
    z-index: 10;
  }
  .toggle_btn span {
    display: block;
    position: absolute;
    left: 0;
    width: 30px;
    height: 2px;
    background-color: #fff;
    border-radius: 4px;
    transition: all .5s;
  }
  .toggle_btn span:nth-child(1) {
    top: 4px;
  }
  .toggle_btn span:nth-child(2) {
    top: 14px;
  }
  .toggle_btn span:nth-child(3) {
    bottom: 4px;
  }
  .open .toggle_btn span {
    background-color: #fff;
  }
  .open .toggle_btn span:nth-child(1) {
    -webkit-transform: translateY(10px) rotate(-315deg);
    transform: translateY(10px) rotate(-315deg);
  }
  .open .toggle_btn span:nth-child(2) {
    opacity: 0;
  }
  .open .toggle_btn span:nth-child(3) {
    -webkit-transform: translateY(-10px) rotate(315deg);
    transform: translateY(-10px) rotate(315deg);
  }
  /*.mask*/
  .mask {
    display: none;
    transition: all .5s;
  }
  .open .mask {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: .8;
    z-index: 2;
    cursor: pointer;
  }
/* Main */
.main-wrapper{
    background-image:url(https://www.pakutaso.com/shared/img/thumb/ogasuta458A7873_TP_V.jpg);
    background-size:cover;
    height:760px;
    margin-top:80px;
}
.fadeIn{
    color:white;
    opacity:0.8;
    font-size:52px;
    font-family:serif;
    text-align:center;
    padding:120px 0;
    animation: fadeIn 2s linear 0s 1 normal;
}
@keyframes fadeIn { /*animetion-nameで設定した値を書く*/

    0% {opacity: 0} /*アニメーション開始時は不透明度0%*/
  
    100% {opacity: 0.8} /*アニメーション終了時は不透明度100%*/
  
  }
/* Introduce */
.introduce-wrapper{
    text-align:center;
    padding:40px 0;
}
.introduce-wrapper h1{
    margin-bottom:60px;
}
.introduce-parts {
    margin-bottom:40px;
}
.introduce-parts li{
    font-size:20px;
    margin:0 auto 20px auto;
    width:50%;
    text-align:left;
}
/* language */
.languages-wrapper{
    display:flex;
    flex-wrap:wrap;
}
.programming-language-wrapper h2{
    padding:80px 0 100px 0;
    font-size:30px;
}
.language-part{
    width:25%;
    margin-bottom:100px;
}
.language-part img{
    width:65%;
}
/* Hobby */
.hobby-wrapper{
    padding-bottom:60px;
}
.hobby-wrapper h1{
    text-align:center;
}
.hobby-sub-wrapper{
    padding:60px;
    display:flex;
    flex-wrap:wrap;
}
.hobby-common-btn{
    padding:20px;
    background-color:rgba(221, 221, 221, 0.705);
    color:black;
    border-radius:5px;
    opacity:1;
    cursor:pointer;
    border:none;
    display:inline-block;
    width:50%;
}
.hobby-common-btn:hover{
    opacity:0.6;
    transition:1s all;
    color:rgb(255, 255, 255);
    background-color:black;
}
.game-image{
    width:80%;
}
.anime-image{
    width:50%;
    height:auto;
    border-radius:25%;
}
.game-image-wrapper{
    width:50%;
    text-align:center;
    position:relative;
}
.anime-image-wrapper{
    width:50%;
    text-align:center;
    position:relative;
}
.game-image-part{
    position:relative;
}
.game-image-part p{
    font-size:20px;
    position:absolute;
    top:89%;
    left:43%;
}
.anime-image-part p{
    font-size:20px;
    margin:10px 0 20px 0;
}
/* Modal */
.modal-wrapper{
    z-index:5;
    background-color:rgb(235, 235, 235,0.9);
    border-radius:10px;
    width:80%;
    padding:20px;
    display:flex;
    flex-direction:column;
    height:420px;
    overflow:auto;
    box-sizing:border-box;
    position:absolute;
    top:0px;
    left:10%;
}
.modal-wrapper2{
    z-index:5;
    background-color:rgb(235, 235, 235,0.8);
    border-radius:10px;
    width:80%;
    padding:20px;
    display:flex;
    flex-direction:column;
    height:420px;
    overflow:auto;
    box-sizing:border-box;
    position:absolute;
    top:0%;
    left:10%;
}
.modal-wrapper h1{
    padding:20px 0 40px 0;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.modal-wrapper2 h1{
    padding:20px 0 40px 0;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.modal-contents{
    margin-bottom:30px;
    text-align:center;
}
.modal-contents img{
    width:90%;
}
.modal-contents p{
    width:90%;
    display:inline-block;
    text-align:left;
    margin:10px 0;
    font-size:20px;
}
.modal-close-button{
    width:80%;
    margin:0 auto;
    padding:10px;
    cursor:pointer;
}
.modal-close-button:hover{
    opacity:0.6;
    color:rgb(255, 255, 255);
    background-color:black;
    transition:1s all;
}
/* challenge */
.challenge-wrapper{
    padding:40px 0 90px 0;
    text-align:center;
}
.challenge-wrapper h1{
    margin-bottom:60px;
}
.challenge-wrapper ul{
    padding:80px;
    background-image:url(https://img.kb-cdn.);
    background-size:cover;
    width:auto;
    height:auto;
    border-radius:70%;
}
.challenge-wrapper ul li{
    font-size:20px;
    width:60%;
    text-align:left;
    display:inline-block;
    margin:0 auto 30px auto; 
    color:rgb(63, 63, 63);
}
/* footer */
.footer-menu-wrapper ul{
    display:flex;
    flex-wrap:wrap;
}
.footer-menu-wrapper a{
    display:inline-block;
    padding:5px;
}
.footer-home{
    border-right:1px solid white;
}
.footer-home li:hover{
    color:yellow;
}
.footer-about{
    border-right:1px solid white;
}
.footer-about li:hover{
    color:yellow;
}
.footer-contact li:hover{
    color:yellow;
}
.footer-menu-wrapper li{
    color:white;
    list-style:none;
    margin:0 30px;
}
.footer-wrapper{
    height:200px;
    background-color:rgb(19, 19, 19);
    display:flex;
    flex-wrap:wrap;
    padding:30px;
}
.fa-icon-wrapper{
    text-align:center;
    padding:30px 0;
}
.fa-icon-wrapper a{ 
    display:inline-block;
    width:55px;
    height:55px;
    background-color:white;
    border-radius:50%;
    color:black;
    margin-right:25px;
    text-align:center;
