}
.fa-facebook:hover{
    transform:scale(1.2, 1.2);
    transition:1s all;
}
.copyright-wrapper{
    text-align:center;
}
.copyright-mini-wrapper{
    display:flex;
    flex-wrap:wrap;
}
.copyright-mini-wrapper span{
    display:block;
    margin-left:60px;
}
.fa-copyright{
    border-radius:50%;
    color:white;
    padding:10px;
    height:12px;
    width:10px;
    margin-right:25px;
